.history{position:relative
}
.history:before{content:'';position:absolute;left:50%;width:1px;height:100%;background:#eee;z-index:-1
}
.history .history_title{margin-bottom:30px
}
.history .year_list{display:-webkit-box;display:-ms-flexbox;display:flex
}
.history .year_list .year_wrap{display:-webkit-box;display:-ms-flexbox;display:flex;width:50%
}
.history .year_list .year_wrap .year{position:relative;-webkit-box-flex:0;-ms-flex:0 0 80px;flex:0 0 80px;color:#a50034;font-size:26px;font-weight:bold;padding:0 0 0 40px
