.history .year_list .year_wrap .month_wrap .month_list{display:-webkit-box;display:-ms-flexbox;display:flex;line-height:2
}
.history .year_list .year_wrap .month_wrap .month_list div{-webkit-box-flex:1;-ms-flex:1 auto;flex:1 auto;font-size:16px
}
.history .year_list .year_wrap .month_wrap .month_list div.month{-webkit-box-flex:0;-ms-flex:0 0 50px;flex:0 0 50px;font-weight:bold
}
.history .year_list:nth-child(odd){-webkit-box-pack:end;-ms-flex-pack:end;justify-content:flex-end
}
.history .year_list:nth-child(even) .year{padding:0 40px 0 0
