.history .year_list:nth-child(even) .year:before{left:auto;right:-7px;-webkit-transform:rotate(180deg);transform:rotate(180deg)
}
.history .year_list:nth-child(even) *{-webkit-box-orient:horizontal;-webkit-box-direction:reverse;-ms-flex-direction:row-reverse;flex-direction:row-reverse;text-align:right
}
.faq_bbs .tab_menu ul{overflow:hidden;border-bottom:1px solid #666
