}
.faq_bbs .tab_menu ul li a.on{color:#333
}
.faq_bbs .tab_menu ul li a.on:before{content:'';position:absolute;left:0;right:0;bottom:0px;height:5px;border-radius:5px 5px 0 0;background-color:#000
}
.faq_bbs .tab_menu+p{padding:32px 0 12px;font-size:16px
