.faq_bbs dl{width:100%;border-top:2px solid #333;border-bottom:1px solid #ddd
}
.faq_bbs dt{position:relative;padding:20px 50px 20px 50px;height:35px;line-height:35px;color:#333;font-weight:500;border-top:1px solid #ddd
}
.faq_bbs dt:before{content:'Q';position:absolute;left:20px;-webkit-transition:color .4s;transition:color .4s
}
.faq_bbs dt:after{content:'';position:absolute;display:block;top:25px;right:30px;width:24px;height:24px;background:url(data:image/svg+xml;base64,) no-repeat;-webkit-transform:rotate(90deg);transform:rotate(90deg);-webkit-transition:-webkit-transform .4s;transition:-webkit-transform .4s;transition:transform .4s;transition:transform .4s, -webkit-transform .4s
}
.faq_bbs dt.on:before{color:#a50034
}
.faq_bbs dt.on:after{-webkit-transform:rotate(-90deg);transform:rotate(-90deg)
}
.faq_bbs dt,.faq_bbs dt+dd{-webkit-transition:background .4s;transition:background .4s
}
.faq_bbs dt.on,.faq_bbs dt.on+dd{background:#eee
}
.faq_bbs dd p{padding:20px 100px 40px 50px
}
.faq_bbs .title{color:#fff
}
.faq_bbs li p{background:#fff
}
